<template>
  <div id="app">
    <div class="container text-center mt-5 bg-light py-4">
      <h2>{{ message }}</h2>
      <h4 class="d-inline pr-2">Tarea</h4>
      <input
        class="mr-2"
        size="45"
        type="text"
        placeholder="ingrese una nueva tarea"
        v-model="lista"
      />
      <button @click="agregarTarea" class="btn btn-dark mr-2">
        Agregar tarea
      </button>
      <button @click="limpiarLista" class="btn btn-dark">Limpiar lista</button>
    </div>
    <event-list :todos="todos" @remove="eliminar"></event-list>
  </div>
</template>

<script>
import todoList from "./TodoList.vue";
export default {
  components: {
    "event-list": todoList,
  },
  data() {
    return {
      message: "Crear Nueva Tarea",
      todos: [],
      lista: "",
    };
  },
  methods: {
    agregarTarea: function () {
    this.todos.push({ text: this.lista });
    },
    limpiarLista: function () {
      this.todos = [];
      this.lista = "";
    },
    eliminar(tarea){
        console.log(this.todos)
        let index = this.todos.findIndex((text)=>text.text===tarea)
        console.log(index)
        this.todos.splice(index,1);
    }
  },
};
</script>

<style scoped>
#app {
  font-size: 20px;
  font-family: "Calibri", sans-serif;
}
#listado {
  margin-left: 300px;
  display: inline-block;
}
</style>