<template>
  <div>
      <h2 class="text-center">Lista de Tareas</h2> 
      <ol id="listado">
        <li v-for="todo in todos" :key="todo.text">
          {{todo.text}}
            <button @click="eliminar(todo.text)" class="btn bg-success">Eliminar</button>
        </li>
      </ol>
  </div>
</template>
<script>
export default {
  props:{
    todos:{
      type:Array,
      required:true,
    }
  },
  methods:{

    eliminar: function(text){
      this.$emit("remove",text)

    },
  }
}
</script>
